import $ from 'jquery';
import 'jquery-sticky/jquery.sticky.js';
import { TweenMax } from 'gsap';
window.jQuery = $;
require('@fancyapps/fancybox/dist/jquery.fancybox');

$('[data-fancybox="gallery"]').fancybox({
  infobar: false
});

let media = $('.js-player-media');

media.on('click', function(e) {
  e.preventDefault();
  let iframe = $(this).children('.js-player-iframe');
  let play = $(this).children('.js-play-iframe');
    
  $('.js-play-iframe').removeClass('hide');
  $('.js-player-iframe').removeClass('is-active').children().attr('src', '');

  play.addClass('hide');
  iframe.addClass('is-active');
  iframe.children().attr('src', iframe.children().data('src'));
});


const burger = $('.js-burger');
const mainNav = $('.js-nav');
const header = $('.js-header');

$(burger).on('click', function() {
  	$(burger).toggleClass('is-active');
  	
  	$(header).toggleClass('is-active');

  	if ( $(mainNav).hasClass('is-active') ) {
  		$(mainNav).toggleClass('is-active');
  	} else {
  		setTimeout( () => {
  			$(mainNav).toggleClass('is-active');
  		}, 300);
  		
  	}
  	
});


$(window).on('resize', (e) => {
  $(header).removeClass('is-active');
  $(burger).removeClass('is-active');
  $(mainNav).removeClass('is-active');
});

